.calendar {
    font-family: Arial, sans-serif;
    width: 100%;
    margin: 0 auto;
  }
  
  .calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #f0f0f0;
  }
  
  .calendar-header button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 16px;
    color: black;
  }
  
  .calendar-header h2 {
    margin: 0;
  }
  
  .calendar-body {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 5px;
    padding: 10px;
  }
  
  .empty-day {
    background-color: #f0f0f0;
  }
  
  .calendar-day {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border: 1px solid #ccc;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .calendar-day:hover {
    background-color: #f0f0f0;
  }
  